import React, { useRef, useEffect } from "react";
import { useGLTF, useAnimations } from "@react-three/drei";

export default function Hand2(props) {
  const group = useRef();
  const { nodes, materials, animations } = useGLTF("/hand2.gltf");
  const { actions } = useAnimations(animations, group);
  useEffect(() => {
    actions.Hand2.play();
  });
  return (
    <group ref={group} {...props} dispose={null}>
      <group
        name="Armature"
        position={[-2.42, 2.65, 0.84]}
        scale={[0.8, 0.8, 0.8]}
      >
        <group position={[-0.3, -2.7, 0.11]} rotation={[-Math.PI, 0, -Math.PI]}>
          <group position={[-0.52, 2.62, 0.01]} rotation={[-0.02, 0, 0.02]}>
            <group position={[0, 0.36, 0]} rotation={[0.01, 0, 0]} />
          </group>
          <group position={[0.01, 2.64, 0]} rotation={[0, -0.02, 0.03]}>
            <group
              position={[0, 0.47, 0]}
              rotation={[0, 0, -0.05]}
              scale={[1, 1, 1]}
            />
          </group>
          <group position={[0.55, 2.6, 0]} rotation={[0, -0.02, -0.03]}>
            <group position={[0, 0.27, 0]} rotation={[0, 0, 0]} />
          </group>
          <group
            position={[-0.54, 1.39, 0]}
            rotation={[0.15, 0.13, 0.82]}
            scale={[1, 1, 1]}
          >
            <group position={[0, 0.48, 0]} rotation={[0, 0, -0.01]} />
          </group>
        </group>
      </group>
      <group position={[1.78, 2.63, 0.9]} scale={[0.8, 0.8, 0.8]}>
        <primitive object={nodes.Bone012_1} />
        <skinnedMesh
          geometry={nodes.HandRight.geometry}
          skeleton={nodes.HandRight.skeleton}
        >
          <meshLambertMaterial skinning={true} color={"#5D2A42"} />
        </skinnedMesh>
        <skinnedMesh
          geometry={nodes.HandRight_1.geometry}
          skeleton={nodes.HandRight_1.skeleton}
        >
          <meshLambertMaterial skinning={true} color={"#CEC075"} />
        </skinnedMesh>
      </group>
    </group>
  );
}

useGLTF.preload("/hand2.gltf");
